/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Row, Col, Button } from 'react-bootstrap';
import { Link } from 'gatsby';
import { Color } from 'src/constants';
import { Menu } from 'src/models';
import { Media } from 'src/utils';

interface Props {
  title: string;
  direction: 'left' | 'right';
  description: string;
  buttonTitle: string;
  menu: Menu;
  photo: string;
}
const MainContentRow = ({
  title,
  direction,
  description,
  buttonTitle,
  menu,
  photo,
}: Props) => {
  return (
    <Row as="section" css={css`
      padding: 2rem 0;
    `}>
      <Col as="h2" xs={12} css={css`
        margin-bottom: 2rem;
      `}>
        {title}
      </Col>
      <Col
        xs={{
          span: 12,
          order: 1,
        }}
        md={{
          span: 6,
          order: direction === 'left' ? 1 : 0,
        }}>
          <p dangerouslySetInnerHTML={{ __html: description }} css={css`
            font-size: .8rem;
            line-height: 1.8;
          `}></p>
          <StyledButton>
            {menu.externalLink ? (
              <a href={menu.link} target="_blank" rel="noopener noreferrer">
                {buttonTitle}
              </a>
            ) : (
              <Link to={menu.link}>{buttonTitle}</Link>
            )}
          </StyledButton>
        </Col>
        <Col
          xs={{
            span: 12,
            order: 0
          }}
          md={{
            span: 6,
            order: direction === 'left' ? 0 : 1,
          }}>
          <div
            css={css`
              padding-bottom: 50%;
              background-size: cover;
              background-image: url(${photo});
              ${Media.mobile} {
                margin-bottom: 1rem;
              }
            `}
          ></div>
        </Col>
    </Row>
  );
}

const StyledButton = styled(Button)`
  background-color: ${Color.BlueGrey900};
  border: none;
  font-size: .8rem;
  padding: .5rem 1.5rem;
  a {
    color: ${Color.White};
  }
`;

export default MainContentRow;
